<template>
    <section class="home" @mousemove="mouseHover()"  id="home">
        
        <div class="image" >
            <img src="./image/h3-slider-banner1.png" class="img-1" alt="">
            <img src="./image/h3-layer1.png" class="img-2" alt="">
            <img src="./image/home-parallax-img.png" class="img-3" alt="">
        </div>

        <div v-for="(content, index) in homeContent" :key="`content - ${index}`" class="content">
                <span class="title">{{ content.span_text }}</span>
                <h3 class="description">
                    {{content.title_text1}}
                        <span>{{content.clorgreen}}</span>
                    {{content.title_text2}}

                    <p>{{content.p_text}}</p>
                </h3>

                <div v-for="(btn, index) in button" :key="`btn - ${index}`" class="btn-flex">
                    <a :href="btn.href1" :class="btn.class"> {{ btn.btn1 }} </a>
                    <a :href="btn.href2" :class="btn.class2"> {{ btn.btn2 }} </a>
                </div>
        </div>
    </section>
</template>

<script>
export default {
    

    name:"comp-home",
    data() {
        return{

            button: [
                {
                    class:"btn",
                    class2:"btn2",
                    btn1:"Shop now",
                    href1:"# shop now",
                    btn2: "About us",
                    href2:" #about us"
                }
            ],

            homeContent:[
                {
                    span_text:"Were are organio",
                    title_text1:"We are the ",
                    clorgreen: "best",
                    title_text2: "service provider",
                    p_text:'Organic food is at the heart of what we do. Our organic whole foods are high quality',
                }
            ]
        }
    },

    methods: {
        mouseHover(){
            document.querySelector('.home').onmousemove = (e) =>{

                let x = (window.innerWidth - e.pageX * 2) / 90;
                let y = (window.innerHeight - e.pageY * 2) / 90;

                document.querySelector('.home .img-3').style.transform = `translateX(${y}px) translateY(${x}px)`;
            }

            document.querySelector('.home').onmouseleave = () =>{

                document.querySelector('.home .img-3').style.transform = `translateX(0px) translateY(0px)`;
            } 
        },

    },
}
</script>

<style lang="scss">
 * {
        margin: 0;
        padding: 0;
        text-decoration: none;
        list-style: none;
        text-transform: none;
        transition: all .2s linear;
    }

    html {
        font-size: 62.6%;

        &::-webkit-scrollbar{
            display: none;
        }
    }
    
    section{
        padding: 2rem 5%;
    }

    .btn-flex{
        display: inline-block;
        margin-top: 3rem;

        .btn{
            padding: 1.2rem 3.2rem;
            background: #3cb815;
            color: #fff;
            font-size: 1.4rem;
            border-radius: 2rem;
            position: relative;
            overflow: hidden;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                width: 0;
                height: 100%;
                background: rgba(255,255,255, .5);
                color: #333;
                opacity: .1;
                transition: .3s linear;
            }

            &:hover{
                    
                &::before {
                    width: 100%;
                    left: 0;
                    border-radius: 2rem;
                    opacity: .8;
                }
            }
        }

        .btn2{
            padding: 1.2rem 3.2rem;
            background: #fff;
            color: #333;
            font-size: 1.4rem;
            border: .2rem solid #232c30;
            border-radius: 2rem;
            margin-left: 1.5rem;

            &:hover {
                background: #3cb815;
                color: #fff;
                border: .2rem solid #3cb815;
            }
        }
    }

    .home{
        padding: 0;
        margin-top: 3rem;
        background: url(./image/h3-bg-slider1.jpg) no-repeat;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 2rem;
        position: relative;
        overflow: hidden;

        .image{
            flex: 1 1 40rem;
            margin: 2rem 0;
            pointer-events: none;

            .img-1 {
                width: 100%;
                margin-top: 5rem;
                
            }

            .img-2{
                position: absolute;
                bottom: 0;
                left: 0;
            }

            .img-3 {
                position: absolute;
                top: -4rem;
                left: -31rem;
                width: 80vw;
                transform: rotate(-17deg);
            }

            
        }

        .content{
            flex: 1 1 40rem;
            max-width: 100rem;
            width: 100%;
            margin: 0 2rem;
            // text-align: center;
            // cursor: pointer;

            .title{
                
                font-size: 2rem;
                text-transform: uppercase;
                color: #46bc21;
            }

            .description{
                font-size: 5vw;
                font-family: 'Satisfy', cursive;

                span{
                    color: #3cb815;
                    text-transform: capitalize;
                }

                p{
                    font-family: "Rubik", sans-serif;
                    font-size: 1.5rem;
                    font-weight:400;
                }
            }
        }
    }

   
</style>